<script lang="ts">
  import * as Paho from 'paho-mqtt';
  import { writable } from 'svelte/store';

  const topic = 'power';
  const power = writable(0);

  // Generate a unique client ID for this client
  const clientId = `clientId-${Math.random().toString(16).substr(2, 8)}`;

  // Create a new MQTT client and connect to the Mosquitto Test server
  const client = new Paho.Client('test.mosquitto.org', 8080, clientId);
  client.onConnectionLost = onConnectionLost;
  client.onMessageArrived = onMessageArrived;
  client.connect({ onSuccess: onConnect });

  function onConnect() {
    console.log('Connected to MQTT server');
    client.subscribe(topic);
  }

  function onConnectionLost(responseObject) {
    if (responseObject.errorCode !== 0) {
      console.log('Connection lost:', responseObject.errorMessage);
    }
  }

  function onMessageArrived(message) {
    if (message.destinationName === topic) {
      power.set(parseInt(message.payloadString));
    }
  }

  // Send updates to the MQTT server when the slider is moved
  function handleSliderChange(event) {
    const message = new Paho.Message(event.target.value);
    message.destinationName = topic;
    client.send(message);
  }
</script>

<template>
  <div>
    <input type="range" min="0" max="100" step="1" bind:value={$power} on:input={handleSliderChange}>
    <p>Power: {$power}</p>
  </div>
</template>